import Helper from '@/function.js';
export default {

	data() {

		return {
			sendType: -1,
			token:"",
			list:[],
			pageIndex:1,
			pageSize:15,
		}
	},
	onLoad:function(){
		this.token = uni.getStorageSync('token');
		
	},
	onShow:function(){
		this.pageIndex= 1;
		this.list=[]
		this.getList();
	},

	methods: {
		btnClick: function(index) {
			console.log(index);
			if (this.sendType == index) {
				return;
			}

			this.sendType = index;
			this.pageIndex = 1;
			this.list=[];
			this.getList();
		},
		goCreate(item){
			// console.log(item);
			uni.navigateTo({
				url:"/pages/mytask/create?item="+JSON.stringify(item)
			})
		},
		goTaskDetails(item){
			
			uni.navigateTo({
				url:"/pages/my/taskDetails?item="+JSON.stringify(item)
			})
		}
		,
		getList() {
			var _this = this;
			
			if (_this.sendType == -1)
			{
				var state = ''
			}
			else
			{
				var state = _this.sendType
			}
			
			var params = {
				pageIndex:_this.pageIndex,
				pageSize: _this.pageSize,					
			}
			uni.showLoading({
				title:"加载中..."
			})
			uni.request({
				url: _this.ApiGateWay + "/sale/Member/GetMyPublishedTasks",
				data:params,
				header: {
					Authorization: _this.token
				},
				method: 'POST',
				dataType: 'json',
				success: function(res) {
					console.log(res);
					if (Helper.CheckData(res)) {
						_this.list = _this.list.concat(res.data.list);
					}
		
				},
				complete() {
					uni.hideLoading();
				},
				fail: function(res) {
					uni.stopPullDownRefresh();
				}
			});
		},
		bank() {

			uni.navigateBack({
				delta: 1
			})
		},



	}

}
